////////////////////////////////////////////////////////////////////////////////////////////////////////////////////////

// layout mixins - start

@mixin edgtfTableLayout() {
    position: relative;
    display: table;
    table-layout: fixed;
    height: 100%;
    width: 100%;
}

@mixin edgtfTableCellLayout($vertical-align: middle) {
    position: relative;
    display: table-cell;
    height: 100%;
    width: 100%;
    vertical-align: $vertical-align;
}

@mixin edgtfRelativeHolderLayout($vertical-align: middle) {
    position: relative;
    display: inline-block;
    width: 100%;
    vertical-align: $vertical-align;
}

@mixin edgtfAbsoluteHolderLayout() {
    position: absolute;
    display: block;
    width: 100%;
    height: 100%;
    top: 0;
    left: 0;
}

@mixin edgtfTypographyLayout() {
    color: inherit;
    font-family: inherit;
    font-size: inherit;
    font-weight: inherit;
    font-style: inherit;
    line-height: inherit;
    letter-spacing: inherit;
    text-transform: inherit;
}

// this shadow is placed on text
@mixin edgtfFontShadow($color: #fff) {
    text-shadow: 0 0 4px rgba($color, 0.5), 0 0 5px rgba($color, 0.5);
}

// this hover shadow is placed on text
@mixin edgtfFontShadowHover($color: #fff) {
    text-shadow: 0 0 16px rgba($color, 0.5), 0 0 25px rgba($color, 0.5), 0 0 7px rgba($color, 0.5); 
}

// this shadow is used for larger shadow
@mixin edgtfFontShadowHuge($color: #fff) {
    text-shadow: 0 0 25px rgba($color, 0.5), 0 0 40px rgba($color, 0.8);
}


// this is classic shadow
@mixin edgtfShadow($color: $first-main-color) {
    box-shadow: 0 0 40px 4px rgba($color,.75);
}

@mixin edgtfShadowSmall($color: $first-main-color) {
    box-shadow: 0 0 4px 2px rgba($color,.75);
}

// ***** button shadow

// this shadow is used for upper left side
@mixin edgtfShadowButton($color: #fff) {
    box-shadow: -5px -5px 20px rgba($color,.15), -5px -5px 20px rgba($color,.15),-5px -5px 20px rgba($color,.15),-5px -5px 20px rgba($color,.15);
}

// this shadow is used for lower right side
@mixin edgtfShadowButtonAfter($color: #fff) {
    box-shadow: 5px 5px 20px rgba($color,.15), 5px 5px 20px rgba($color,.15), 5px 5px 20px rgba($color,.15), 5px 5px 20px rgba($color,.15);
}


// layout mixins - end

////////////////////////////////////////////////////////////////////////////////////////////////////////////////////////

// transition mixins - start

@mixin edgtfTransition($transition-param...) {
    -webkit-transition: $transition-param;
    -moz-transition: $transition-param;
    transition: $transition-param;
}

@mixin edgtfTransitionTransform($transition-param...) {
    -webkit-transition: -webkit-transform $transition-param;
    -moz-transition: -moz-transform $transition-param;
    transition: transform $transition-param;
}

@mixin edgtfTransform($transform-param...) {
    -webkit-transform: $transform-param;
    -moz-transform: $transform-param;
    transform: $transform-param;
}

@mixin edgtfAnimation($animation-param...) {
    -webkit-animation: $animation-param;
    -moz-animation: $animation-param;
    animation: $animation-param;
}

@mixin edgtfTransformOrigin($animation-param...) {
    -webkit-transform-origin: $animation-param;
    -moz-transform-origin: $animation-param;
    transform-origin: $animation-param;
}

// transition mixins - end

