// common mixins - start

@mixin edgtfBckImageStyle() {
    background-size: cover;
    background-repeat: no-repeat;
    background-position: center center;
}

@mixin edgtfImageOverlayHoverStyle($with-hover: true) {
    @if ($with-hover) {
        &:hover {
            &:after {
                opacity: 1;
            }
        }

        &:after {
            @include edgtfAbsoluteHolderLayout();

            content: '';
            background-color: rgba(#000, .8);
            opacity: 0;

            @include edgtfTransition(opacity .2s ease-in-out);
        }

    } @else {
        @include edgtfAbsoluteHolderLayout();

        content: '';
        background-color: rgba(#000, .8);
        opacity: 0;

        @include edgtfTransition(opacity .2s ease-in-out);
    }
}

@mixin edgtfStandardPaginationStyle($list_type: null) {
    @include edgtfRelativeHolderLayout(top);

    margin: 40px 0 0;
    clear: both;

    ul {
        @include edgtfRelativeHolderLayout(top);

        padding: 0;
        margin: 0;
        list-style: none;
        text-align: center;

        li {
            position: relative;
            display: inline-block;
            vertical-align: top;

            a {
                position: relative;
                display: block;
                vertical-align: top;
                margin: 0;
	            padding: 6px 17px;
                color:#fff;
            }

            &.edgtf-pag-active {
                a {
                    color: $first-main-color;
	                border-bottom: 7px solid $first-main-color;
	                margin: 0 5px;
                }
            }

            &:not(.edgtf-pag-active) {
                a:hover {
                    @include edgtfFontShadow($first-main-color);
                }
            }

            &.edgtf-pag-prev,
            &.edgtf-pag-next {
	            position: absolute;
	            
	            a {
		            padding: 6px 0;
	            }
            }
	
	        &.edgtf-pag-prev {
		        left:0;
	        }
	
	        &.edgtf-pag-next {
	            right:0;
	        }
        }
    }
}

@mixin edgtfButtonDefaultStyle() {
    position: relative;
    display: inline-block;
    vertical-align: middle;
    width: auto;
    margin: 0;
    font-family: inherit;
    font-size: 16px;
    line-height: 2em;
    letter-spacing: 0.14em;
    font-weight: 700;
    text-transform: uppercase;
    outline: none;
    box-sizing: border-box;

    border-radius: 0;
    -webkit-appearance: none;

    @include edgtfTransition(color .2s ease-in-out, background-color .2s ease-in-out, border-color .2s ease-in-out, box-shadow .2s ease-in-out);
}

@mixin edgtfButtonSize($size: medium) {
    @if ($size == 'small') {
        padding: 14px 40px;

    } @else if ($size == 'medium') {
        padding: 17px 50px;

    } @else if ($size == 'large') {
        padding: 22px 66px;

    } @else if ($size == 'huge') {
        display: block;
        text-align: center;
        padding: 22px 26px;
    }
}

@mixin edgtfButtonTransparentColor() {
    color: $default-text-color;
    background-color: transparent;
}

@mixin edgtfButtonSolidColor($important: null) {
    color: $second-main-color $important;
    background-color: $default-heading-color $important;
    border: 1px solid transparent $important;
}

@mixin edgtfButtonSolidHoverColor($important: null) {
    color: #fff $important;
    background-color: $second-main-color $important;
}

@mixin edgtfButtonOutlineColor($important: null) {
    color: $first-main-color $important;
    background-color: transparent $important;
    border: 1px solid $first-main-color $important;
}

@mixin edgtfButtonOutlineHoverColor($important: null) {
    color: #fff $important;
    background-color: $first-main-color $important;
    border-color: $first-main-color $important;
}

@keyframes edgtf-glow-pulse-anim {
	to {
		text-shadow:3px 3px 23px rgba($first-main-color,.85),-3px -3px 23px rgba($first-main-color,.85),1px -3px 35px rgba($first-main-color,.85),-3px 3px 35px rgba($first-main-color,.85);
	}
}

@keyframes edgtf-arrow-glow-pulse-anim {
	to {
		text-shadow: 0 0 30px rgba($second-main-color,1), 0 0 30px rgba($second-main-color,1), -1px -1px 37px rgba($second-main-color, 1), 2px 2px 37px rgba($second-main-color, 1);
	}
}

@mixin edgtfReadMore() {
	position: relative;
	
	.edgtf-btn-text {
		//@include edgtfFontShadowHuge($first-main-color);
		text-shadow:3px 3px 13px rgba($first-main-color,.55),-3px -3px 13px rgba($first-main-color,.55),1px -3px 15px rgba($first-main-color,.45),-3px 3px 15px rgba($first-main-color,.45);
		padding-right: 30px;
        -webkit-appearance: none;
	}
	
	&:after {
		content:'â–¶';
		position: absolute;
		display: block;
		right:0;
		top:2px;
		color: $second-main-color;
		text-shadow: 0 0 30px rgba($second-main-color,.55), -1px -1px 25px rgba($second-main-color, 1), 2px 2px 25px rgba($second-main-color, 1);
		@include edgtfTransform(scaleX(.5));
        -webkit-appearance: none;
	}
 
	&:hover {
		.edgtf-btn-text {
			animation: edgtf-glow-pulse-anim .75s infinite alternate cubic-bezier(0.09, 0.68, 0.75, 0.85);
		}
		
		&:after {
			animation: edgtf-arrow-glow-pulse-anim .75s infinite alternate cubic-bezier(0.09, 0.68, 0.75, 0.85);
		}
	}
 
}

@mixin edgtfPlaceholder {
    &::-webkit-input-placeholder {
        @content
    }

    &:-moz-placeholder {
        @content;

        opacity: 1;
    }
    &::-moz-placeholder {
        @content;

        opacity: 1;
    }
    &:-ms-input-placeholder {
        @content
    }
}

// common mixins - end

